<template>
  <div id="app" ref="app">
    <template v-if="!tooSmall">
      <aside :class="[{thin: isPlaying }]">
        <NavBar :class="[{thin: isPlaying }]" />
      </aside>
      <keep-alive :exclude="['Run', 'Results']">
        <router-view class="main" />
      </keep-alive>
      <PlayersList v-if="room.connected && $route.path !== '/run'" />
    </template>
    <SmallScreen v-else />
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import PlayersList from '@/components/PlayersList.vue';
import SmallScreen from '@/views/SmallScreen.vue';
import { mapGetters } from 'vuex';

export default {
  components: {
    NavBar,
    SmallScreen,
    PlayersList,
  },
  computed: {
    ...mapGetters(['room']),
    tooSmall() {
      return this.windowWidth < 640 || this.windowHeight < 480;
    },
    isPlaying() {
      return this.$route.path === '/run';
    },
  },
};
</script>

