  padding: 0.1em
  display: grid
  grid-template-columns: repeat(4, 1fr)
  gap: 0.2rem
  background: transparent radial-gradient(200px 200px at var(--mouse-x) var(--mouse-y), rgba($blue, 0.2), transparent) no-repeat 0 0
  text-align: center

.language-radio
  display: none

.language
  cursor: pointer
  user-select: none
  padding: 1em
  background: $navy-grey
  opacity: 0.95

  // border: 1px solid grey
  // box-shadow: 0px 0px 2px 0px rgb(var(--navy-grey), .1)
  display: flex
  justify-content: space-around
  align-items: center

  &:hover
    opacity: 0.85

.stat
  padding: 0.2em
  opacity: 0
  color: $blue
  font-size: 0.9em
  display: flex
  flex-direction: column
  justify-content: space-between

.language:hover > .stat
  opacity: 1

.highlight
  background: linear-gradient(30deg, var(--accent1), var(--accent2))

</style>
