export default {
  data() {
    return {
      rootEl: document.querySelector(':root'),
    };
  },
  methods: {
    trackMouse(ev) {
      if (!this.rafActive && this.element) {
        this.rafActive = true;

        requestAnimationFrame(() => {
          this.rafActive = false;
          console.log(this.element.tagName, this.element.className);
          const x = ev.pageX - this.element.offsetLeft;
          const y = ev.pageY - this.element.offsetTop;
          this.element.style.setProperty('--mouse-x', `${x}px`);
          this.element.style.setProperty('--mouse-y', `${y}px`);
        });
      }
    },
  },
};
